<template>
  <div class="swiper-box">
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item v-for="(image) in images" :key="image.bannerId">
        <img v-lazy="image.imgUrl" />
      </van-swipe-item>
    </van-swipe>
  </div>
</template>

<script>
import Vue from 'vue';
import { Lazyload } from 'vant';

Vue.use(Lazyload);
export default {
  name: 'MovieSwiper',
  data() {
    return {
      images: [],
    }
  },
  mounted () {
    this.getSwiperArr()
  },
  methods: {
    async getSwiperArr() {
      const res = await this.$api.GetSwiper()
      // console.log(res.data);
      this.images=res.data

    }
  },
}
</script>

<style lang="less" scoped>
.swiper-box {
  width: 375px;
  height: 210px;
  // background-color: pink;
  .my-swipe {
    width: 100%;
    height: 100%;
    img {
      height: 100%;
    }
  }
}
</style>
